<script>
/**
 * Icon
 * @module packages/Icon
 * @desc svg
 * @desc 
 * @rules 
 *   -  mode
 *   -  -o  question-circle  question-circle-o
 *   - []-[?]-[?]-[?]
 * @param {string} [mode] -  [font, svg, canvas, custom]
 * @param {string} type - icon  Name
 * @param {number} [size] - , svg iconfont 
 * @param {color} [color] - 
 *
 * @example
 * <Icon mode="svg" type="String" />
 * <Icon mode="font" type="String" />
 * <Icon mode="custom" type="String" />
 */
import PropTypes from 'vue-types'

export default {
  name: 'kit-icon',

  props: {
    prefixCls: String,
    type: PropTypes.string.isRequired,
    spin: Boolean,
    size: [String, Number],
    mode: PropTypes.oneOf([
      'font',
      'svg',
      'canvas',
      'custom',
    ]).def('svg'),
    shape: PropTypes.oneOf([
      'circle',
      'radius',
      'square',
    ]),
    bg: String,
    color: String,
    fill: String,
    reverse: Boolean,
  },

  computed: {
    classes () {
      const {
        mode,
        type,
        spin,
        shape,
        reverse,
      } = this.$props

      let { prefixCls = 'icon' } = this.$props
      prefixCls = mode ? `${prefixCls}-${mode}` : `${prefixCls}-svg`
      // this.prefixCls = prefixCls
      return {
        // icon: true,
        [`${prefixCls}`]: true,
        [`${prefixCls}-${type}`]: !!type,
        [`${prefixCls}-${reverse}`]: reverse,
        [`is-${shape}`]: !!shape,
        [`is-spin`]: spin || type === 'loading',
      }
    },
    styles() {
      const {
        size,
        color,
        bg,
      } = this.$props
      return {
        fontSize: `${size}px`,
        color: !!color,
        backgroundColor: !!bg,
      }
    },
    iconType() {
      const { mode, type } = this.$props
      if (mode !== 'svg') return

      const warnMsg = `Icon ${type} is invalid, have you set svg-sprite-loader correctly? see https://github.com/kisenka/svg-sprite-loader`
      if (!type || typeof type !== 'string') {
        console.error(warnMsg)
        return null
      }

      let xlinkHref = this.renderSvg()
      if (!/^#/.test(xlinkHref || type)) {
        console.error(warnMsg)
      }
      // let outerIcon
      if (!xlinkHref) {
        // outerIcon = true
        xlinkHref = type
      } else {
        xlinkHref = `#${type}`
      }
      // console.log(xlinkHref)
      return xlinkHref
    },
    //  px
    svgStyles() {
      const { size, fill } = this.$props
      let sizeStyle = {}
      if (size) {
        // var size = '280, 70'
        // console.log(size.split(/\s*,\s*| +/))
        const [width, height = width] = size.split(/\s*,\s*| +/)
        sizeStyle = {
          width, // svg 
          height,
        }
      }
      return {
        fill,
        ...sizeStyle,
      }
    },

  },

  methods: {
    renderSvg() {
      const { type, mode } = this.$props
      if (mode !== 'svg') return
      let svg
      try {
        svg = require(`../assets/svg/${type}.svg`)
      } catch (e) {
        svg = {}
        // console.log(e)
      } finally {
        /* eslint no-unsafe-finally: 0 */
        return svg.default
      }
    },
  },

  render(h) {
    const { mode } = this.$props

let iconNode = null
    //  slot 
    switch (mode) {
      case 'font': {
        // {} const 
        const { classes, styles } = this
        iconNode = (
          <i class={ classes } style={ styles }></i>
        )
        break
      }
      // case 'canvas': {
      //   iconNode = 'IconCanvas'
      //   break
      // }
      // case 'custom': {
      //   //  css icon
      //   iconNode = 'IconCustom'
      //   break
      // }
      case 'svg':
      default: {
        const { classes, svgStyles, iconType } = this
        iconNode = (
          <svg class={ classes } style={ svgStyles } title={ iconType } aria-hidden='true'>
            <use xlinkHref={ iconType }></use>
          </svg>
        )
      }
    }

    return iconNode
  },
}
</script>

<style lang="stylus" scope>
.icon-svg {
  width: 1em;
  height: 1em;
  fill: currentColor;
  // transform: inherit; // 
}
.icon-font {
  font-size: 1em;
  color: currentColor;
}
// .icon-custom  icon-custom.styl
// .icon-after::after,
// .icon-before::before
//   content ''
//   display inline-block
//   width 12px
//   height 12px
//   color inherit

// .icon-arrow::after
//   width 9px
//   height 9px
//   border-width 1px 1px 0 0
//   border-color currentColor
//   border-style solid
//   transform matrix(0.71, 0.71, -0.71, 0.71, 0, 0)

</style>



